import { Component, ElementRef, Inject, OnInit, Optional, ViewChild } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { MatPaginator } from '@angular/material/paginator';
import { MatSort } from '@angular/material/sort';
import { MatStepper } from '@angular/material/stepper';
import { MatTableDataSource } from '@angular/material/table';
import { DomSanitizer } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { FormListComponent } from '@fragments';
import { Affiliation, Company, UserAccount, UserRole } from '@models';
import { ApiService, DataStorageService, SweetAlertService } from '@services';
import { ACCOUNT_TYPE, ENDPOINTS, USER_ROLES } from '@shared';
import { ESignature } from 'app/core/models/eSignature.model';
import { FileStorageService } from 'app/core/services/file.storage.service';
import { ValidateFieldService } from 'app/core/services/validate-field.service';
import { NgxSpinnerService } from 'ngx-spinner';
import Swal from 'sweetalert2';
import { AddCompanyComponent } from '../../company/add-company/add-company.component';

@Component({
  selector: 'app-user-account',
  templateUrl: './user-account.component.html',
  styleUrls: ['./user-account.component.scss']
})
export class UserAccountComponent implements OnInit {
  @ViewChild(FormListComponent) formListComponent: FormListComponent;
  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  @ViewChild('stepper') stepper: MatStepper;
  @ViewChild('selectFileButton', { static: false }) selectFileButton: ElementRef;
  dataSource: MatTableDataSource<UserAccount>;
  displayedColumns: string[] = [
    'firstName',
    'lastName',
    'companyDescription',
    'emailAddress',
    'mobileNumber',
    'status'
  ];
  currentTab: string = "NONASC";
  userAccount = new UserAccount();
  company = new Company();
  userRole = new UserRole();
  affiliation = new Affiliation();
  eSign = new ESignature();

  userAccountForm: FormGroup;
  userInfo: FormGroup;
  companyInfo: FormGroup;
  eSignature: FormGroup;
  accountInfo: FormGroup;
  userAccountList = [];
  userRoleList = [];
  companyList = [];
  affiliationList = [];

  displayTable = false;
  isShowUploadButton = false;
  previewDocument = false;
  safeURLSignature: any;
  selectedFile: File;
  fileSize: number;
  uploadedSignatureFile: any;

  passwordMatch = false;
  hidePassword = true;
  hideConfirmPassword = true;
  confirmPasswords: any;
  isFromUserProfile = false;
  currentUserRole: any;

  isAscUser: boolean;
  //isUpdateAscUser: boolean;

  constructor(
    private apiService: ApiService,
    private formBuilder: FormBuilder,
    private sweetAlertService: SweetAlertService,
    private dataStorageService: DataStorageService,
    public validateFieldService: ValidateFieldService,
    private router: Router,
    private fileStorageService: FileStorageService,
    private spinner: NgxSpinnerService,
    private sanitizer: DomSanitizer,
    private dialog: MatDialog,
    @Optional() public dialogRef: MatDialogRef<UserAccountComponent>,
    @Optional() @Inject(MAT_DIALOG_DATA) public data: any) {
    if (data) {
      this.isFromUserProfile = data.isFromProfile;
      this.userAccount = data.userAccount;
      this.viewUserInformation();
    }

  }

  ngOnInit(): void {
    this.setFormValidator();
    this.getUserPerTypeList();
    this.getMaintenanceList();
    // alert(this.isFromUserProfile)
    this.currentUserRole = this.dataStorageService.getUserRole();

  }

  save(): void {
    this.userAccount.username = this.userAccount.username.toLowerCase();
    if (this.currentUserRole === 'ROLE_SUPER_ADMIN' || this.currentUserRole === 'ROLE_ADMIN') {
      this.userAccount.company = null;
    }
    // this.apiService.save(ENDPOINTS.addUserAccount, this.userAccount).subscribe(
    this.apiService.save(ENDPOINTS.userAccount, this.userAccount).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.customSuccessMessage('Account has been created successfully.');
          const evt = { index: null };
          if (this.userAccount.userRole.name === USER_ROLES.ROLE_APPLICANT) {
            evt.index = 0;
          } else {
            evt.index = 1;
          }
          this.accountInfo.reset();
          if (this.isFromUserProfile) {
            this.dialog.closeAll();
          }
        }
      }, (err: any) => {
        this.sweetAlertService.customErrorMessage(err?.error?.message ? err.error.message : err.error);
        console.log('ERROR:::', err);

      }, () => {
        this.resetFormValidator();
        if(this.currentTab=="NONASC")this.getUserPerTypeList();
        else if(this.currentTab=="ASC")this.getUserPerTypeList(ACCOUNT_TYPE.ASC_USER);
        this.userAccount = new UserAccount();
      });
  }

  update(): void {
    console.log('update')
    const endpoint = this.userAccount.userRole.name === USER_ROLES.ROLE_APPLICANT ? ENDPOINTS.userAccount : ENDPOINTS.updateUserAccount;
    this.apiService.update(endpoint, this.userAccount).subscribe(
      (res: any) => {
        if (res !== undefined) {
          // this.updateCredentials();
          this.sweetAlertService.customSuccessMessage('Account was successfully updated');
          const evt = { index: null };
          console.log(this.userAccount);

          if (this.userAccount.userRole.name === USER_ROLES.ROLE_APPLICANT) {
            evt.index = 0;
          } else {
            evt.index = 1;
          }
          // this.changeTab(evt);
          this.accountInfo.reset();
          
          if (this.isFromUserProfile) {
            this.dialog.closeAll();
          }
        }
      }, (err: any) => {
        console.log(err);
        this.sweetAlertService.error(err);
      }, () => {
        
        if(this.currentTab=="NONASC")this.getUserPerTypeList();
        else if(this.currentTab=="ASC")this.getUserPerTypeList(ACCOUNT_TYPE.ASC_USER);
        this.userAccount = new UserAccount();
        this.resetFormValidator();
      });
  }


  insert(isNewEntry: boolean): void {
    this.userAccount.username = this.userAccount.username.toLowerCase();
    if (this.currentUserRole === 'ROLE_SUPER_ADMIN' || this.currentUserRole === 'ROLE_ADMIN') {
      this.userAccount.company = null;
    }
    // this.apiService.save(ENDPOINTS.addUserAccount, this.userAccount).subscribe(
    this.apiService.insert(ENDPOINTS.userAccount, this.userAccount, isNewEntry).subscribe(
      (res: any) => {
        if (res !== undefined) {
          this.sweetAlertService.customSuccessMessage(
            isNewEntry? 'Account has been created successfully.': 'Account was successfully updated'
          );
          const evt = { index: null };
          if (this.userAccount.userRole.name === USER_ROLES.ROLE_APPLICANT) {
            evt.index = 0;
          } else {
            evt.index = 1;
          }
          this.accountInfo.reset();
          if (this.isFromUserProfile) {
            this.dialog.closeAll();
          }
        }
      }, (err: any) => {
        this.sweetAlertService.customErrorMessage(err?.error?.message ? err.error.message : err.error);
        console.log('ERROR:::', err);

      }, () => {
        this.resetFormValidator();
        if(this.currentTab=="NONASC")this.getUserPerTypeList();
        else if(this.currentTab=="ASC")this.getUserPerTypeList(ACCOUNT_TYPE.ASC_USER);
        this.userAccount = new UserAccount();
      });
  }

  updateCredentials() {
    const updatedAccount = {
      password: this.userAccount.password,
      username: this.userAccount.username,
      userRole: this.userAccount.userRole
    }
    this.apiService.update(ENDPOINTS.updatePassword, updatedAccount).subscribe((res: any) => {
      if (res !== undefined) {
        this.sweetAlertService.success(res);
        const evt = { index: null };
        if (this.userAccount.userRole.name === USER_ROLES.ROLE_APPLICANT) {
          evt.index = 0;
        } else {
          evt.index = 1;
        }
        this.changeTab(evt);
        this.accountInfo.reset();
      }
    });
  }

  delete(): void {

  }

  getSelectedCompany(i): void {
    if (i) {
      this.apiService.findById(ENDPOINTS.company, i.value.id).subscribe(
        (res: any) => {
          this.company = res.responseData.data;
        });
    }
  }

  getUserPerTypeList(type = 'APPLICANT'): void {
    this.spinner.show();
    this.displayTable = false;

    this.isAscUser = type !== 'APPLICANT';

    this.apiService.findAll(`${ENDPOINTS.userAccountType}/${type}`).subscribe(
      (res: any) => {
        this.userAccountList = res.responseData.data;
        this.displayTable = true;
        this.spinner.hide();
        this.dataSource = new MatTableDataSource<UserAccount>(res.responseData.data);
        // this.dataSource.paginator = this.formListComponent.paginator;
        // this.dataSource.sort = this.formListComponent.sort;
      }
    )
  }

  getMaintenanceList(): void {
    this.apiService.findAll(ENDPOINTS.activeCompanies).subscribe((res: any) => {
      this.companyList = res.responseData.data;
    }, (err: any) => {
    });
    this.apiService.findAll(ENDPOINTS.activeMembersAffiliation).subscribe((res: any) => {
      this.affiliationList = res.responseData.data;
    }, (err: any) => {
    });
    this.apiService.findAll(ENDPOINTS.roles).subscribe((res: any) => {
      this.userRoleList = res.responseData.data;
    }, (err: any) => {
    });

  }

  compareMembersAffiliation(value: any, selected: any): boolean {
    if (value && selected) {
      return value.description === selected.description;
    }
  }

  compareCompany(value: any, selected: any): boolean {
    if (value && selected) {
      return value.id === selected.id;
    }
  }

  compareUserRole(value: any, selected: any): boolean {
    if (value && selected) {
      return value.id === selected.id;
    }
  }

  confirmPassword(password): void {
    if (this.userAccount.password === password) {
      this.passwordMatch = true;
    } else {
      this.passwordMatch = false;
    }
  }

  // get password() {
  //   let password = this.accountInfo.controls["password"].value;
  //   if (password === null || password === '' || password === undefined) {
  //     this.accountInfo.controls['password'].clearValidators();
  //     this.accountInfo.controls["confirmPasswords"].clearValidators();
  //   } else {
  //     this.accountInfo.controls['password'].setValidators([Validators.required, Validators.pattern('((?=.*\\d)(?=.*[a-z])(?=.*[A-Z]).{8,30})'), Validators.minLength(8)]);
  //     this.accountInfo.controls["confirmPasswords"].setValidators([Validators.required]);
  //   }
  //   this.accountInfo.controls['password'].updateValueAndValidity();
  //   this.accountInfo.controls['confirmPasswords'].updateValueAndValidity();
  //   return this.accountInfo.controls["password"].value
  // }
  viewUserInformation(): void {
    // if (this.isAscUser || this.userAccount.id) {
    //   this.stepper.selectedIndex = 0;
    // }
    const accountId = this.formListComponent?.idForUpdate ?
    this.formListComponent.idForUpdate : this.userAccount.id;

    this.eSign.signatureAttachmentReference = null;
    this.eSign.fileURL = null;
    this.eSign.originalFilename = null;
    this.apiService.findById(ENDPOINTS.userAccount, accountId).subscribe(
      (res: any) => {
        this.userAccount = res.responseData.data;
        this.company = res.responseData.data.company;
        this.affiliation = this.userAccount.company.membersAffiliation;
        this.userRole = this.userAccount.userRole;
        if (this.userAccount.signatureAttachmentReference) {
          this.getEsignature()
        } else {
          this.selectFileButton.nativeElement.value = '';
        }
      });
  }

  openCompanyComponent(): void {
    const dialogRef = this.dialog.open(AddCompanyComponent, {
      width: '80%',
      height: '85%',
      data: {
        isFromModal: true,
        isUserRegistration: true
      }
    });
    dialogRef.afterClosed().subscribe(result => {
      console.log('The dialog was closed', result);
      if (result) {
        this.company = result;
      }
      this.getUserPerTypeList();
    });
  }

  setFormValidator(): void {
    this.userInfo = this.formBuilder.group({
      firstName: ['', Validators.required],
      middleName: [''],
      lastName: ['', Validators.required],
      mobileNumber: ['', Validators.required],
      userRole: [''],
      company: [''],
    });
    this.eSignature = this.formBuilder.group({
      eSignature: ['', Validators.required],
    });

    if (!this.isFromUserProfile) {
      this.accountInfo = this.formBuilder.group({
        userName: ['', [Validators.required, Validators.pattern('[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}')]],
        // userRole: ['', Validators.required],
        userRole: [''],
        password: ['', [Validators.required, Validators.pattern('((?=.*\\d)(?=.*[a-z])(?=.*[A-Z]).{8,30})'), Validators.minLength(8)]],
        confirmPasswords: ['', Validators.required],
      });
    } else {
      this.accountInfo = this.formBuilder.group({
        userName: ['', [Validators.required, Validators.pattern('[a-zA-Z0-9.-_]{1,}@[a-zA-Z.-]{2,}[.]{1}[a-zA-Z]{2,}')]],
        password: ['', Validators.required],
        confirmPasswords: ['', Validators.required],
      });
    }
    this.userAccountForm = new FormGroup({
      userInfo: new FormControl(),
      companyInfo: new FormControl(),
      eSignature: new FormControl(),
      accountInfo: new FormControl(),
    });
  }

  resetFormValidator(): void {
    this.userAccount = new UserAccount();
    this.company = new Company();
    this.affiliation = new Affiliation();
    this.previewDocument = false;
    this.userAccountForm.markAsUntouched();
    this.userAccountForm.markAsPristine();
    this.dialog.closeAll();
    if (this.isFromUserProfile) {
      this.dialog.closeAll();
    } else {
      this.router.navigate(['asc/page/maintenance/accounts']);
    }

  }

  changeTab(evt) {
    this.userAccount = new UserAccount();
    this.resetFormValidator();
    if (evt.index === 0) {
      this.currentTab="NONASC";
      //     this.isUpdateAscUser = false;
      this.getUserPerTypeList();
      this.displayedColumns = [
        'firstName',
        'lastName',
        'companyDescription',
        'emailAddress',
        'mobileNumber',
        'status'
      ];
    } else if (evt.index === 1) {
      this.currentTab="ASC";
      //     this.isUpdateAscUser = true;
      this.getUserPerTypeList(ACCOUNT_TYPE.ASC_USER);
      this.displayedColumns = [
        'firstName',
        'lastName',
        'userRole',
        'emailAddress',
        'status'
      ];
    }
  }

  selectFile(event) {
    this.isShowUploadButton = false;

    if (event.target.files[0]) {
      this.fileSize = event.target.files[0].size;

      if (this.fileSize > 15000000) {
        this.sweetAlertService.customErrorMessage(`Attachments total file size should not exceed 15MB.`);
        event.target.value = '';
        return;

      } else {
        this.isShowUploadButton = true;
        const fileURL = URL.createObjectURL(event.target.files[0]);
        this.previewDocument = true;

        this.safeURLSignature = this.sanitizer.bypassSecurityTrustResourceUrl(fileURL);

        const reader = new FileReader();
        const [file] = event.target.files;

        reader.readAsDataURL(file);
        reader.onload = () => {
        };

        this.selectedFile = event.target.files[0];
      }
    }
  }

  viewDocument() {
    if (this.uploadedSignatureFile) {
      window.open(this.uploadedSignatureFile.fileURL);
    } else {
      window.open(this.eSign.fileURL);
    }
  }

  uploadSignature() {
    Swal.fire({
      text: 'Are you sure you want to upload this as e-Signature?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.spinner.show();

        // TODO: CHANGE ATTACHMENT TYPE WHEN SIGNATURE IS AVAILABLE IN BE
        const accountId = this.formListComponent?.idForUpdate ?
          this.formListComponent.idForUpdate : this.userAccount.id;
        const params = `?accountId=${accountId}`;

        this.fileStorageService.pushFileToStorage(ENDPOINTS.signature, this.selectedFile, params)
          .subscribe((res: any) => {
            this.uploadedSignatureFile = res.responseData.data;

            this.addToESignature(this.uploadedSignatureFile);

            this.userAccount.signatureAttachmentReference = this.uploadedSignatureFile.signatureAttachmentReference;
            this.updateUserSignatureReference();
            this.hideSpinner();
            this.sweetAlertService.customSuccessMessage('Signature successfully uploaded.');
            this.selectedFile = null;

            this.getEsignature();
            this.isShowUploadButton = false;

          }, (err: any) => {
            this.hideSpinner();
            this.sweetAlertService.error(err);
          }, () => {
            this.hideSpinner();
          });
      } else {
        this.selectedFile = null;
      }
    });
  }

  updateUserSignatureReference() {
    const endpoint = this.isAscUser ? ENDPOINTS.userAccount : ENDPOINTS.updateUserAccount;
    this.apiService.update(endpoint, this.userAccount).subscribe(
      (res: any) => {
        console.log(res);
      });
  }

  addToESignature(obj: any) {
    this.eSign.signatureAttachmentReference = obj.signatureAttachmentReference;
    this.eSign.id = obj.id;
    this.eSign.originalFilename = obj.originalFilename;
    this.eSign.filename = obj.filename;
    this.eSign.fileExtension = obj.fileExtension;
    this.eSign.fileURL = obj.fileURL;
    this.eSign.accountId = obj.accountId;
    this.eSign.documentFormat = obj.documentFormat;
  }

  hideSpinner() {
    setTimeout(() => {
      this.spinner.hide();
    }, 1);
  }

  getEsignature() {
    const accountId = this.formListComponent?.idForUpdate ?
      this.formListComponent.idForUpdate : this.userAccount.id;
    this.spinner.show();

    this.apiService.findByParam(`${ENDPOINTS.signature}/account-id`, accountId).subscribe(
      (res: any) => {
        if (res) {
          this.eSign = res?.responseData?.data;
        }
        this.hideSpinner();
      }, (err: any) => {
        this.hideSpinner();
        console.error(err);
      });
  }

  resendEmail(): void {
    this.apiService.save(ENDPOINTS.adminResendVerificationCode + `/${this.userAccount.username}`, this.userAccount).subscribe(
      (res: any) => {
        this.sweetAlertService.customSuccessMessage('Email verification has been sent');
      }
    )
  }

  deleteESignature(): void {
    Swal.fire({
      text: 'Are you sure you want to upload this as e-Signature?',
      icon: 'question',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirm'
    }).then((result) => {
      if (result.value) {
        this.spinner.show();
        this.apiService.deleteById(ENDPOINTS.signature, this.eSign?.id).subscribe(
          res => {
            this.eSign = new ESignature();
            this.previewDocument = false;
            this.spinner.hide();
          }, err => this.spinner.hide());
      }
    });
  }

}
